
import avatarSrc from '../userHeader/avator.png'
import Scroll from '../../../base/scroll/scroll'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { userInfoMixin } from '../../../assets/js/mixin'
export default {
name: "settings",
mixins: [userInfoMixin],
components: {
Group,
Cell,
Scroll,
Actionsheet,
Datetime
},
created() {

if (this.userInfo.depositPassword) {
this.boxTwo[0].state = 1
this.boxTwo[0].isLink = false
this.boxTwo[0].value = ''
}
},
computed: {
boxOne: {
get() {
let arr = []

if (this.userInfo.taobaoId) {
let obj = {
title: `${this.userInfo.taobaoId}`
}
if (this.userInfo.taobaoStatus === 1) {
obj.state = 2
obj.isLink = false
obj.value = ''
} else if (this.userInfo.taobaoStatus === 0) {
obj.state = 0
obj.isLink = true
obj.value = ''
obj.link = '../registerThree'
} else {
obj.state = 1
obj.isLink = true
obj.value = ''
}
arr.push(obj)
} else {
let obj = {
title: '',
link: '../registerThree',
isLink: true,
value: '',
info: '',
state: 1
}
arr.push(obj)
}

if (this.userInfo.bankCardNo) {
let reg = /^(\d{4})[\d]*(\d{4})$/
let account = this.userInfo.bankCardNo.replace(reg, "$1^-^$2");
let obj = {
title: `${account}`,
}
if (this.userInfo.bankCheckStatus === 1) {
obj.state = 2
obj.isLink = false
obj.value = ''
} else if (this.userInfo.bankCheckStatus === 0) {
obj.state = 0
obj.isLink = true
obj.value = ''
link: 'userSafe/yinHangKa'
} else {
obj.state = 1
obj.isLink = true
obj.value = ''
}
arr.push(obj)
} else {
let obj = {
title: '',
link: 'userSafe/yinHangKa',
isLink: true,
value: '',
info: '',
state: 0
}
arr.push(obj)
}
return arr
},
set(val) {
return val
}
},
boxTwo: {
get() {
let arr = []

if (this.userInfo.depositPassword) {
let obj = {
title: '',
state: 1,
isLink: false,
value: ''
}
arr.push(obj)
} else {
arr.push({
title: '',
link: 'userSafe/setPayPwd',
isLink: true,
value: '',
info: ''
})
}
arr.push(
{
title: '',
link: 'userSafe/updateUserPwd',
isLink: true,
value: '@',
info: ''
}
)
return arr
},
set(val) {
return val
}
}
},
data() {
return {
stateClass: ['classError', 'classProgress', 'classSuccess']
}
},
methods: {

isNull(value) {
if (value === '@') {
return ''
} else if (value) {
return value
}
return ''
}
}
}



